import React from 'react';
import { Switch, Route } from 'react-router-dom';
import { Home, CO2Excel, CO2JAVA, Water } from './index';

const Router = () => (
    <Switch>
        <Route exact path="/" component={Home} />
        <Route path="/portfolio/co2excel" component={CO2Excel} />
        <Route path="/portfolio/co2java" component={CO2JAVA} />
        <Route path="/portfolio/water" component={Water} />
    </Switch>
);

export default Router;